import React, { Component } from "react";
import { observer } from "mobx-react";
import TextField from "material-ui/TextField";

@observer
class EnsemblTextField extends Component {
  render() {
    return (
      <TextField
        id={this.props.id}
        placeholder="Search for a species or assembly"
        style={{
          padding: "16px 32px",
          boxSizing: "border-box",
          borderWidth: 0,
          height: "auto",
          borderRadius: "8px",
          backgroundColor: "#fff",
          fontSize: "22px",
          color: "#ededed",
          width: "100%",
          fontFamily: "'Roboto', sans-serif"
        }}
        underlineStyle={{ display: "none" }}
      />
    );
  }
}

export default EnsemblTextField;



