import React, { Component } from "react";
import MuiThemeProvider from "material-ui/styles/MuiThemeProvider";
import { observer } from "mobx-react";
import Styles from "./Styles";
import EBISCLogo from "./ebisc.png";
import HIPSCILogo from "./hipsci.png";
import { Row, Col } from "react-grid-system";
import Store from "./Store";
import Button from "./Button";
import EnsemblTextField from "./EnsemblTextField";
import TextField from "material-ui/TextField";

const APIURL = "https://gti-es-0.ebi.ac.uk:8080/";

@observer
class App extends Component {
  render() {
    return (
      <MuiThemeProvider>
        <div>
          <div className="container padding">
            <Row>
              <Col sm={9}>
                <Button label="Add a species" />
              </Col>
              <Col sm={3}>
                <b>OREO</b> Custom data download tool
              </Col>
            </Row>
          </div>

          <Row style={{ background: "#E0E0E0" }}>
            <div className="container padding">
              <h1>Add a species or assembly to start</h1>
              <div
                style={{
                  paddingLeft: "90px",
                  paddingTop: "20px",
                  paddingBottom: "90px"
                }}>
                <EnsemblTextField id="speciesSearch" />
              </div>

              <Button light={true} label="Search by taxonomy" />
            </div>
          </Row>

          <Row
            style={{
              position: "absolute",
              left: "0",

              bottom: "0",
              right: "0",
              background: "#E0E0E0"
            }}>
            <div className="container padding">
              <Col sm={9}>ensembl.org</Col>
            </div>
          </Row>
        </div>
      </MuiThemeProvider>
    );
  }
}

export default App;



